/**
 * 
 * @param {object} obj 
 *  {
 *      title: '',
 *      icon:  'success', 'none',
 *      duration: 1500,
 *      mask: true
 *  }
 *  Promise
 */ 
export function showToast (obj) {
    let {
        title = '', 
        icon = 'success', 
        image, 
        duration = 1500, 
        mask = true
    } = obj || {}
    return new Promise((resolve, reject) => {
        wx.showToast({
            title,
            icon,
            image,
            duration,
            mask,
            success: resolve,
            fail: reject
        })
    })
}

