/**
 * 
 * @param {function} fn 
 * @param {number} delay 
 */
export function debounce(fn, delay = 500) {
    let timer = null
    return function () {
        let _self = this
        let args = [].slice.call(arguments)
        clearTimeout(timer)
        timer = setTimeout(function () {
            fn.apply(_self, args)
        }, delay)
    }
}

/**
 * 
 * @param {function} fn 
 * @param {number} wait 
 */
export function throttle(fn, wait = 500) {
    let lastTime = 0 // 
    return function () {
    	let args = [].slice.call(arguments) // 
        let nowTime = new Date().getTime() // 
        if(nowTime - lastTime > wait) { 
            fn.apply(this, args)
            lastTime = nowTime // 
        }
    }
}

/**
 * 
 * @param {array} data 
 * @param {function} fn 
 * @param {number} count 
 * @param {number} delay 
 */
export function timeChunk(data, fn, count = 20, delay = 200) {
    let timer
    function start() {
        for(let i = 0; i < Math.min(count, data.length); i++) {
            let obj = data.shift()
            fn(obj)
        }
    }
    return function () {
        timer = setInterval(function () {
            if(data.length === 0) {
                return clearInterval(timer)
            }
            start()
        }, delay)
    }
}

/**
 *  
 * @param {number} n
 */
export function digitUppercase(n) {
    var fraction = ['', '']
    var digit = [
        '', '', '', '', '',
        '', '', '', '', ''
    ]
    var unit = [
        ['', '', ''],
        ['', '', '', '']
    ]
    var head = n < 0 ? '' : ''
    n = Math.abs(n)
    var s = ''
    for (var i = 0; i < fraction.length; i++) {
        s += (digit[Math.floor(n * 10 * Math.pow(10, i)) % 10] + fraction[i]).replace(/./, '')
    }
    s = s || ''
    n = Math.floor(n)
    for (var i = 0; i < unit[0].length && n > 0; i++) {
        var p = ''
        for (var j = 0; j < unit[1].length && n > 0; j++) {
            p = digit[n % 10] + unit[1][j] + p
            n = Math.floor(n / 10)
        }
        s = p.replace(/(.)*$/, '').replace(/^$/, '') + unit[0][i] + s
    }
    return head + s.replace(/(.)*/, '')
        .replace(/(.)+/g, '')
        .replace(/^$/, '')
}

/**
 * 
*/
export function decimalAdd(num1, num2) {  
    //
    let decimalLen1 = (num1.toString().split('.')[1] || '').length
    //
    let decimalLen2 = (num2.toString().split('.')[1] || '').length
    let baseNum = Math.pow(10, Math.max(decimalLen1, decimalLen2))
    return (num1 * baseNum + num2 * baseNum) / baseNum
}

/**
 * 
 * @param {string} start  23.099579542217775,113.24026330500826
 * @param {string} end    23.099579542217775,113.24026330500826
 */
export function getDistance(start, end) {
    let lon1 = (Math.PI / 180) * start[1];
    let lon2 = (Math.PI / 180) * end[1];
    let lat1 = (Math.PI / 180) * start[0];
    let lat2 = (Math.PI / 180) * end[0];
    // 
    let R = 6371;
    //  km*1000
    let d = Math.acos(Math.sin(lat1) * Math.sin(lat2) + Math.cos(lat1) * Math.cos(lat2) * Math.cos(lon2 - lon1)) *
        R;
    return d * 1000;
}

/**
 * v4UUID.
 * @returns {string}
 */
export function uuid() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, (c)=> {
        const r = (Math.random() * 16) | 0,
            v = c === 'x' ? r : (r & 0x3) | 0x8;
        return v.toString(16);
    });
}

