/**
 * 
 * @param {string} username
 */
export function getUsernameColor(username, color = COLORS) {
    // 
    let hash = 7
    for (var i = 0, len = username.length; i < len; i++) {
        hash = username.charCodeAt(i) + (hash << 5) - hash
    }
    // 
    let index = Math.abs(hash % color.length)
    return color[index]
}

