/**
 * 
 * @param {string} name 
 * @param {string} data 
 */
export function cloudFn(name, data) {
    return new Promise(async (resolve, reject) => {
        try {
            wx.showLoading({
                title: 'Loading...', //,
                mask: true, //,
            })
            let {
                result
            } = await wx.cloud.callFunction({
                name,
                data
            })
            if(result.code > 0) throw result
            resolve(result)
        } catch (error) {
            console.log(error)
            reject(error)
        } finally {
            wx.hideLoading()
        }
    })
}

